	module.exports = {"data":{"post":{"html":"<p><a href=\"http://www.flickr.com/photos/papascott/158812779/\" title=\"Photo Sharing\"><img src=\"http://static.flickr.com/37/158812779_c3ab00bc46_m.jpg\" width=\"240\" height=\"180\" alt=\"02062006024\" /></a></p>\n<p>We just returned from camping. The weather was perfect: mostly sunny, slightly breezy, dry, and warm enough for short sleeves.</p>\n<p>Unfortunately, it was only perfect for one hour as we were packing to leave. Otherwise, the weather was mostly miserable. Rain on Saturday, wind on Sunday, and <a href=\"http://www.wetter.com/v2/?SID=&amp;LANG=DE&amp;LOC=7003&amp;LOCFROM=0202&amp;type=WORLD&amp;id=45769&amp;size=&amp;requested_values%5B%5D=temp_c&amp;start_d=03&amp;start_m=06&amp;start_y=2006&amp;end_d=05&amp;end_m=06&amp;end_y=2006&amp;local_utc=local&amp;x=33&amp;y=3\">mostly cold</a> for June (low was 5&deg;C this morning). </p>\n<p><a href=\"http://www.flickr.com/photos/papascott/159438612/\" title=\"Photo Sharing\"><img src=\"http://static.flickr.com/76/159438612_7e7294ad78_m.jpg\" width=\"240\" height=\"180\" alt=\"03062006025\" /></a></p>\n<p>We made do the best we could. Here's the crew huddled in one of the tents. </p>\n<p>On Sunday we visited the <a href=\"http://www.erlebnisbahn-ratzeburg.de/\">Erlebnisbahnof Schmilau</a>, an old railway station converted into a living musueum. We didn't feel up to a 2-hour trip with a handcar, but we did rent a <a href=\"http://6teambike.com/\">6teambike</a>, a bicycle built for six, for an hour-long round trip.</p>","fields":{"title":"Stormy Weather","date":"05 Jun 2006","url":"https://papascott.de/2006-06-05-stormy-weather/"}}},"pathContext":{"slug":"/2006-06-05-stormy-weather/","prev":{"fields":{"title":"MB 13/1.83 CTO WHITE","date":"02 Jun 2006","slug":"/2006-06-02-mb-13183-cto-white/"}},"next":{"fields":{"title":"Never Talk to Tele2","date":"06 Jun 2006","slug":"/2006-06-06-never-talk-to-tele2/"}}}}

